import './UserInfo.css'
import {useEffect,useState,useRef,useCallback} from 'react'

const UserInfo=()=>{
      const [fig,setfig]=useState(null as any);
      // const



  return(
    <>
    <head>
        <title>UserInfo</title>
    </head>
    <body>
        <div className="Content-Main">
            <form  action="" method="post" className="form-userInfo">
                <h1></h1>
                <span className="text1"></span>
                <div className="fileInputContainer">
                    <input className="fileInput" id="" type="file" name="" />
                </div>

                <label>
                    <span>:</span>
                    <input type="text"  name="name" placeholder="Your Full Name"/>
                </label>
                <label className="Main-sex">
                    <span>:</span>
                    <input type="checkbox" className="man"/>
                    <input type="checkbox" className="women"/>
                </label>
                <label>
                    <span> :</span>
                    <input type="email" name="email" placeholder="@.com"/>
                </label>
                <label>
                    <span>:</span>
                    <input type="text" name="phone" placeholder="Please input 11 number"/>
                </label>
                <label>
                    <span>:</span>
                    <textarea id="message" name="message" placeholder="Your message to us"></textarea>
                </label>
                <label>
                    <input type="button" className="button" value=""/>
                </label>
            </form>
        </div>
</body>
</>
  )
}

export default UserInfo
